.wrap{
    display: flex;
    position: relative;
    height: inherit;
    padding: 12px;
    border: 1px solid #fff;
    border-radius: 9px;
    box-shadow: 0 0 3px rgba(0, 0, 0, .25);
    box-sizing: border-box;
    background: #FFF;
}

.avatar{
    width: auto;
    height: 100%;
    margin: 0 40px  0 0;
    border-radius: 9px;
    overflow: hidden;
}

.name{
    font-size: 28px;
    font-weight: bold;

    margin: 0 0 5px 0;
}

.bio{
    font-size: 14px;
    line-height: 115%;

    margin: 0 0 10px 0;

    color: #999;
}

.contacts{
    font-size: 12px;
    line-height: 115%;

    display: flex;

    margin: 0 0 9px 0;

    color: #999;

    align-items: center;
}

.contactsIcon{
    width: 16px;
    height: 16px;
    padding: 0 4px  0 0;
}

.link{
    text-decoration: none;

    color: #999;

    cursor: pointer;
}

.link:hover{
    text-decoration: underline;

    color: #356eff;
}

.socials{

    display: flex;
    position: absolute;
    bottom: 10px;
    right: 12px;
    color: #999;
}

.socialsIcon{
    width: 24px;
    height: 24px;
    margin: 5px;
}

.socialsIcon:hover{
    opacity: 0.5;
}

